import React from 'react';
import * as path from "path";
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import classNames from 'classnames';
import Icon from '@material-ui/core/Icon';
import PictureAsPdfIcon from '@material-ui/icons/PictureAsPdf';
import SendIcon from '@material-ui/icons/Send';
import MailOutlineIcon from '@material-ui/icons/MailOutline';
import SchoolIcon from '@material-ui/icons/School';
import WorkIcon from '@material-ui/icons/Work';
import Responsive from 'react-responsive';
import IconButton from '@material-ui/core/IconButton';

const Desktop = props => <Responsive {...props} minWidth={660} />;
const Mobile = props => <Responsive {...props} maxWidth={660} />;

const styles = {
  card: {
    maxWidth: 700,
    width: '95%',
    padding: 10,
    margin: 'auto',
    marginBottom: 10,
    marginTop: 10,
  },
  title: {
    fontSize: 14,
  },
  pos: {
    marginBottom: 12,
  },
  icon:{
    marginLeft: 10,
  },
  actions:{
    display: 'block',
    textAlign: 'center',
    alignItems: 'center',
    justifyItems: 'center',
    margin: 'auto',
  },
  media:{
    height: '30%',
    width: '30%',
    borderRadius: '50%',
    display:'block',
    margin:'auto',
  }
};

function About(props) {
  const { classes } = props;
  return (
    <Card className={classes.card}>
      <CardMedia
        component="img"
        alt="Conlon Novak"
        className={classes.media}
        image={path.join(process.env.PUBLIC_URL, "resources", "portrait.jpg")}
        title="Conlon Novak"
      />
      <CardContent>
        <Desktop>
          <Typography gutterBottom variant="h2">
            Conlon Novak
          </Typography>
        </Desktop>
        <Mobile>
          <Typography gutterBottom variant="h4">
            Conlon Novak
          </Typography>
        </Mobile>
        <SchoolIcon className={classes.icon} color='primary'/>
        <Typography variant="h5">
          Carnegie Mellon University
        </Typography>
        <Typography variant="subtitle1">
          <Desktop>B.S. Information Systems and Human-Computer Interaction<br/></Desktop>
          <Mobile>B.S. Information Systems,<br/>Human-Computer Interaction<br/></Mobile>
          Minor in Film and Media Studies<br/>
          Class of 2020
        </Typography>
      </CardContent>
      <CardActions className={classes.actions} disableActionSpacing>
        <Button variant="contained" size="large" color="primary" href={path.join(process.env.PUBLIC_URL, "resources", "Conlon_Novak_Resume.pdf")}>
          Resume
          <PictureAsPdfIcon className={classes.icon} />
        </Button>
        <br/>
        <br/>
        <Desktop>
          <Button size="large" color="primary" href="https://github.com/conlonnovak">
            Github
            <Icon className={classNames(classes.icon, 'fab fa-github fa-inverse fa-sm')} color="primary"/>
          </Button>
          <Button size="large" color="primary" href="https://devpost.com/conlonnovak">
            Devpost
            <Icon className={classNames(classes.icon, 'fab fa-dev fa-inverse fa-lg')} color="primary"/>
          </Button>
          <Button size="large" color="primary" href="https://www.linkedin.com/in/conlon-novak/">
            LinkedIn
            <Icon className={classNames(classes.icon, 'fab fa-linkedin-in fa-inverse fa-sm')} color="primary"/>
          </Button>
          <Button size="large" color="primary" href="mailto:conlonnovak+website@cmu.edu">
            Contact
            <Icon className={classNames(classes.icon, 'fas fa-envelope fa-inverse fa-sm')} color="primary"/>
          </Button>
        </Desktop>
        <Mobile>
          <IconButton className={classNames(classes.icon, 'fab fa-github fa-inverse fa-lg')} color="primary" href="https://github.com/conlonnovak"/>
          <IconButton className={classNames(classes.icon, 'fab fa-dev fa-inverse fa-lg')} color="primary" href="https://devpost.com/conlonnovak"/>
          <IconButton className={classNames(classes.icon, 'fab fa-linkedin-in fa-inverse fa-lg')} color="primary" href="https://www.linkedin.com/in/conlon-novak/"/>
          <IconButton className={classNames(classes.icon, 'fas fa-envelope fa-inverse fa-lg')} color="primary" href="mailto:conlonnovak+website@cmu.edu"/>
        </Mobile>
      </CardActions>
    </Card>
  );
}

About.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(About);